import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import logIn from '@/pages/logIn.vue'
import home from "@/pages/manage/home.vue"
import base from "@/pages/baseLayout.vue"
import shopList from "@/pages/manage/shopList.vue"
import addShop from "@/pages/manage/addShop.vue"
const routes=[{
	path: '/',
	component: logIn	
},
{
	path:'/base',
	component:base,
	redirect: '/base/home',
	children:[
		{
			path:'home',
			component:home
		},{
			path:'shopList',
			component:shopList
			
		},{
			path:"addShop",
			component:addShop
		}
	]
}


]
export default new Router({
	routes:routes
})